import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';

import Start from './panels/Start';
import VolMain from './panels/Vol_main';
import OrgMain from './panels/Org_main';
import OrgEv from './panels/Org_ev';
import OrgVol from './panels/Org_vol';
import OrgDescr from './panels/Org_descr';
import OrgTask from './panels/Org_task';
import { Panel } from '@vkontakte/vkui';
import axios from 'axios';

export default class App extends React.Component {

	state ={
		activePanel: 'start',
		fetchedUser: null,
		events: {data: 1},
		event: {},
		vols: [],
		tasks: [],
		status: 3
	};

	get_events = () =>{
		axios.get("https://vkmanage.herokuapp.com/events/register?status=${this.state.status}&id=${this.state.fethedUser.id}")
			.then(res=>{
				this.setState({events: res});
		});
	};

	get_event = () =>{
		// axios.get("https://vkmanage.herokuapp.com/events/register/", this.state.fetchedUser)
		// 	.then(res=>{
		// 		this.setState({events: res});
		// });
	};

	get_vols = () =>{
		// axios.get("https://vkmanage.herokuapp.com/events/register/", this.state.fetchedUser)
		// 	.then(res=>{
		// 		this.setState({events: res});
		// });
	};

	useEffect = (() => {
		connect.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			console.log(123)
			const usr = await connect.sendPromise('VKWebAppGetUserInfo');
			this.setState({fetchedUser: usr});
		}
		fetchData();
	}, []);

	go = e => { 
		console.log(this.state.fetchedUser)
		console.log(this.useEffect)
		if (e == 'volMain')
			axios.post("https://vkmanage.herokuapp.com/volunteers/register/", this.state.fetchedUser)
				.then(res=>{
					this.setState({state:0})
			});
		else if (e = 'orgMain')
			axios.post("https://vkmanage.herokuapp.com/organizer/register/", this.state.fetchedUser)
				.then(res=>{
					this.setState({state:1})
			});
		this.setState({activePanel: e});
	};

	componentDidMount(){
		//this.useEffect();
		//this.get_events();
		
	}

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Start id='start' fetchedUser={this.state.fetchedUser} go={this.go}/>
				<VolMain id='volMain' go={this.go} events={this.state.events} get={this.get_events}/>
				<OrgMain id='orgMain' go={this.go} events={this.state.events} get={this.get_events}/>
				<OrgEv id='orgEv' go={this.go} event={this.state.event} get={this.get_event}/>
				<OrgVol id='orgVol' go={this.go} vols={this.state.vols} get={this.get_vols}/>
				<OrgDescr id='orgDescr' go={this.go} event={this.state.event} get={this.get_descr}/>
				<OrgTask id='orgTask' go={this.go} tasks={this.state.tasks} get={this.get_descr}/>
			</View>
		);
	}

}

